import instance from "../../api/index.js"
export default {
	data() {
		return {
			scrollHeight: '500px',
			token:"",
			leftArray: [
			],
			// mainArray: [],
			topArr: [],
			leftIndex: 0,
			isMainScroll: false,
			scrollInto: '',
			tipsTop: '0px',
			left:[],
			main:[]
		}
	},
	onLoad() {
		this.token=uni.getStorageSync('token')
		instance({
			url:"/api/product/productCategories",
			data:{
				token:this.token
			},
			method:'POST',
			header:{
			   'content-type':'application/json',
			}
		}).then(res=>{
			console.log(res)
			this.leftArray=res.data.data
			res.data.data.forEach(item=>{
				this.left.push(item.name)
			})
		})
		uni.getSystemInfo({
			success: (res) => {
				/* 设置当前滚动容器的高，若非窗口的告诉，请自行修改 */
				this.scrollHeight = `${res.windowHeight}px`;
				console.log('gaodu', res.windowHeight)
			}
		});
	},
	computed: {

	},
	mounted() {
		// this.getListData();

	},
	methods: {
		/* 获取列表数据 */
		// getListData() {
		// 	/* 因无真实数据，当前方法模拟数据 */
		// 	let [left, main] = [
		// 		[],
		// 		[]
		// 	];

			// for (let i = 0; i < 8; i++) {
			// 	left.push(`${i+1}类商品`);//left数组是2级标题

			// 	let list = [];
			// 	for (let j = 0; j < (i + 1); j++) {
			// 		list.push(j);
			// 	}
			// 	main.push({
			// 		title: `第${i+1}类商品标题`,
			// 		list
			// 	})
			// 	console.log("mian" + main)
			// }
			// this.mainArray = main;
			
		// 	this.$nextTick(() => {
		// 		this.getElementTop();
		// 	})
		// },
		//获取距离顶部的高度
		getScrollTop(selector) {
			return new Promise((resolve, reject) => {
				let query = uni.createSelectorQuery().in(this);
				query.select(selector + '').boundingClientRect(data => {
					resolve(data.top)
				}).exec();
			})
		},
		/* 获取元素顶部信息 */
		async getElementTop() {
			/* Promise 对象数组 */
			let p_arr = [];

			/* 遍历数据，创建相应的 Promise 数组数据 */
			for (let i = 0; i < this.mainArray.length; i++) {
				const resu = await this.getScrollTop(`#item-${i}`)
				p_arr.push(resu)
			}

			// console.log('p_arr', p_arr)

			/* 主区域滚动容器的顶部距离 */
			this.getScrollTop("#scroll-el").then((res) => {
				let top = res;



				/* 所有节点信息返回后调用该方法 */
				Promise.all(p_arr).then((data) => {
					console.log('滚动', data)
					this.tipsTop = `${data}px`;
					this.topArr = data;
				});
			})
		},

		/* 主区域滚动监听 */
		mainScroll(e) {
			if (!this.isMainScroll) {
				return;
			}
			let top = e.detail.scrollTop;
			let index = -1;

			if (top >= this.topArr[this.topArr.length - 1]) {
				index = this.topArr.length - 1;
			} else {
				index = this.topArr.findIndex((item, index) => {
					return this.topArr[index + 1] >= top;
				});
			}
			this.leftIndex = (index < 0 ? 0 : index);
		},
		/* 主区域触摸 */
		mainTouch() {
			this.isMainScroll = true;
		},
		/* 左侧导航点击 */
		leftTap(e) {
			let index = e.currentTarget.dataset.index;
			this.isMainScroll = false;
			this.leftIndex = Number(index);
			this.scrollInto = `item-${index}`;
		}
	}
}
